* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  scroll-behavior: smooth;
  height: 100vh;
  font-family: Poppins, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-family: Roboto, sans-serif;
  font-weight: 700;
}

.general-container {
  background-color: #f8f9fa;
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  display: flex;
}

.left-container {
  background-color: #f8f9fa;
  width: 10%;
  height: 100vh;
  position: relative;
}

.center-container {
  background-color: #f5f5f5;
  width: 80%;
  height: 100vh;
  margin: 0 auto;
}

.icon {
  align-items: left;
  text-align: left;
  flex-direction: row;
  display: flex;
}

.svg {
  margin: 10px;
}

.title-time {
  min-width: 70px;
  display: inline-block;
}

.local-time {
  left: -5px;
}

.titleSection {
  text-align: center;
  padding-top: 40px;
  line-height: 2;
}

.titleSection h1 svg {
  vertical-align: middle;
}

.year-date {
  vertical-align: middle;
  display: inline-block;
}

.mainSection {
  text-align: center;
  padding: 50px;
  position: relative;
}

.taskInputBox {
  color: #202124;
  box-sizing: border-box;
  text-align: center;
  border: 1px solid #dcdcdc;
  border-radius: 24px;
  width: 50%;
  padding: 12px 16px;
  font-size: 16px;
  font-style: italic;
  line-height: 2;
  transition: all .3s;
}

.taskInputBox:focus {
  border-color: #4285f4;
  outline: none;
  box-shadow: 0 0 8px #4285f466;
}

.taskInputBox:focus::placeholder {
  opacity: 0;
}

.taskInputBox:hover {
  border-color: #4285f4;
  box-shadow: 0 0 8px #4285f433;
}

.taskList {
  text-align: left;
  width: 50%;
  margin: 40px auto;
  padding-left: 5px;
}

.task-item {
  margin-bottom: 10px;
  padding-left: 5px;
  font-size: 16px;
  line-height: 1.3;
  overflow: visible;
}

.clearTaskBtn {
  color: #ebfbee;
  box-sizing: border-box;
  cursor: pointer;
  z-index: 1000;
  background-color: #dee2e6;
  border: none;
  border-radius: 24px;
  outline: none;
  padding: 10px 12px;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s;
  position: absolute;
  bottom: 4vh;
  right: 12vw;
}

.clearTaskBtn:hover {
  color: #f8f9fa;
  background-color: #212529;
  transform: scale(1.1);
}

.panel-button-container {
  height: 20%;
  position: relative;
}

.side-panel-button {
  color: #ebfbee;
  box-sizing: border-box;
  transform-origin: center;
  background-color: #dee2e6;
  border: none;
  border-radius: 24px;
  outline: none;
  padding: 1vw 2vw;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s ease-in;
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translate(-50%);
}

.side-panel-button:hover {
  color: #f8f9fa;
  background-color: #212529;
  outline: 5px solid #fff;
  box-shadow: 0 -4px 20px #00000080;
}

.side-panel {
  width: 22.3vh;
  height: 12vh;
  margin-top: 100px;
  font-weight: bold;
  transition: transform .3s;
  position: fixed;
  left: -22.3vw;
}

.panel-item {
  color: #dee2e6;
  cursor: pointer;
  border-radius: 36px;
  width: 10vw;
  height: 2.6vw;
  padding-left: 1vw;
  font-size: 100%;
  line-height: 36px;
  list-style-type: none;
  transition: all .3s;
}

.panel-item:hover {
  color: #f8f9fa;
  background-color: #343a40;
  box-shadow: 0 4px 10px #000c;
}

.right-container {
  background-color: #f8f9fa;
  width: 10%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.moving-message {
  text-align: center;
  z-index: 1000;
  width: 100%;
  font-size: 20px;
  font-weight: 700;
  animation: 5s ease-in-out infinite moveUp;
  position: absolute;
  bottom: -100px;
}

.bitcoin-title {
  font-size: 16px;
  font-weight: 600;
}

.bitcoin-container, .ethereum-container, .tether-container, .solana-container, .bnb-container, .dogecoin-container {
  text-align: center;
  width: 100%;
  transition: all .3s smooth;
  font-size: 12px;
  font-weight: 400;
  animation: 25s linear infinite moveUp;
  position: absolute;
}

.bitcoin-container {
  animation-delay: 0s;
}

.ethereum-container {
  animation-delay: 5s;
}

.solana-container {
  animation-delay: 10s;
}

.tether-container {
  animation-delay: 15s;
}

.dogecoin-container {
  animation-delay: 20s;
}

.family-general-container, .professional-general-container {
  background-color: #f5f5f5;
  width: 100%;
  height: 100vh;
  position: relative;
}

.second-general-container {
  text-align: center;
  background-color: #f5f5f5;
  width: 100%;
  height: 100vh;
  padding-top: 24px;
  position: relative;
}

.back-to-homepage {
  color: #ebfbee;
  box-sizing: border-box;
  cursor: pointer;
  z-index: 1000;
  background-color: #dee2e6;
  border: none;
  border-radius: 24px;
  outline: none;
  padding: 10px 12px;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s;
  position: absolute;
  bottom: 4vh;
  right: 12vw;
}

.back-to-homepage:hover {
  cursor: pointer;
  background-color: #212529;
  box-shadow: 0 0 5px #495057;
}

.news {
  margin-bottom: 10px;
  font-size: 48px;
}

#country-button {
  border: none;
  outline: none;
  margin: 2px;
  padding: 4px 8px;
  font-size: 15px;
}

#country-button:hover {
  cursor: pointer;
  border-radius: 12%;
  padding: 4px 8px;
  box-shadow: 0 0 5px #495057;
}

#country-button:active {
  cursor: progress;
  box-shadow: 0 0 5px #495057;
}

.news-render {
  text-align: left;
  background-color: #f1f3f5;
  border-radius: 5%;
  flex-direction: column;
  gap: 10px;
  width: 60%;
  height: 70vh;
  margin: 40px auto;
  padding-top: 20px;
  display: flex;
}

.news-content {
  box-sizing: border-box;
  padding-left: 30px;
  font-size: 16px;
  line-height: 1.5;
}

.news-render a {
  box-sizing: border-box;
  padding-bottom: 12px;
  padding-left: 56px;
  font-size: 10px;
}

@keyframes moveUp {
  0% {
    bottom: -100px;
  }

  100% {
    bottom: 100%;
  }
}

.firework {
  opacity: 0;
  background-color: #ffeb3b;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  animation: 5s ease-in-out infinite firework-burst, 5s infinite random-fireworks;
  position: absolute;
  box-shadow: 0 0 5px #ffeb3b, 0 0 10px #ffeb3b, 0 0 15px #ffeb3b;
}

@keyframes firework-burst {
  0% {
    opacity: 1;
    transform: scale(0)translate(0);
  }

  5% {
    opacity: 1;
    transform: scale(0)translate(0);
  }

  15% {
    opacity: 1;
    transform: scale(0)translate(0, 5px);
  }

  25% {
    opacity: 1;
    transform: scale(2)translate(0, 10px);
  }

  50% {
    opacity: 1;
    transform: scale(3)translate(0);
  }

  75% {
    transform: scale(4)translate(0);
  }

  100% {
    opacity: 0;
    transform: scale(0)translate(0);
  }
}

@media (width <= 430px) {
  .general-container {
    background-color: #f8f9fa;
    grid-template-rows: 8fr 2fr;
    grid-template-columns: 2fr 8fr;
    width: 100%;
    height: 100vh;
    margin: 0 auto;
    display: grid;
  }

  .left-container {
    background-color: #f8f9fa;
    grid-area: 1 / 1 / 2 / 2;
    width: 20vw;
    height: 80vh;
    margin: 0;
  }

  .center-container {
    background-color: #f8f9fa;
    grid-area: 1 / 2 / 2 / 3;
    width: 80vw;
    height: 80vh;
    margin: 0;
  }

  .right-container {
    background-color: #f8f9fa;
    grid-area: 2 / 1 / 3 / 3;
    width: 100vw;
    height: 20vh;
    margin: 0;
  }
}
